"use client";
import React from "react";

const Navbar = () => {
  const scrollToSection = (id: string) => {
    const section = document.getElementById(id);
    if (section) {
      window.scrollTo({
        top: section.offsetTop,
        behavior: "smooth",
      });
    }
  };

  return (
    <nav className="fixed top-0 left-0 w-full bg-background p-4 text-white shadow-md z-50 transition-all duration-300 ease-in-out">
      <ul className="flex space-x-8 justify-center items-center">
        <li>
          <button
            onClick={() => scrollToSection("home")}
            className="hover:text-primary transition-all duration-300 cursor-pointer"
          >
            Home
          </button>
        </li>
        <li>
          <button
            onClick={() => scrollToSection("about")}
            className="hover:text-primary transition-all duration-300 cursor-pointer"
          >
            About
          </button>
        </li>
        <li>
          <button
            onClick={() => scrollToSection("skills")}
            className="hover:text-primary transition-all duration-300 cursor-pointer"
          >
            Skills
          </button>
        </li>
        <li>
          <button
            onClick={() => scrollToSection("projects")}
            className="hover:text-primary transition-all duration-300 cursor-pointer"
          >
            Projects
          </button>
        </li>
        <li>
          <button
            onClick={() => scrollToSection("contact")}
            className="hover:text-primary transition-all duration-300 cursor-pointer"
          >
            Contact Me
          </button>
        </li>
      </ul>
    </nav>
  );
};

export default Navbar;
